		 -->
    <view class="item20 item1-20" v-if="pageIndex == 1">
      <u-image
        width="100%"
        height="200px"
        style="border-top-left-radius: 10px; border-top-right-radius: 10px"
        src="https://datastream.affectai.cn/callmaterial/images/page20-1024/img13.png"
