<template>
	<view class="page122-wrap" @click="next">
		<!--  -->
		
		<view class="content">
			<view style="font-size: 18px;color: #fff;text-align: center;margin-bottom: 30px;">
				
				<typewriter
				  text=""
				  :speed="100"
				  @finished="finished1"
				></typewriter>
			</view>
			<view style="font-size: 18px;color: #fff;text-align: center;margin-bottom: 30px">
				<typewriter
					v-if="seta"
					text=""
					:speed="100"
					@finished="finished2"
				></typewriter>
			</view>
			<view style="font-size: 18px;color: #fff;text-align: center;margin-bottom: 30px">
				<typewriter
					v-if="setb"
					text=""
					:speed="100"
					@finished="finished3"
				></typewriter>
			</view>
			<view style="font-size: 18px;color: #fff;text-align: center;">
				<typewriter
					v-if="setc"
					text=""
					:speed="100"
					@finished="finished4"
				></typewriter>
				</view>
		</view>
	</view>
</template>

<script>
	import typewriter from "@/pages/componentsA/jp-typewriter/index.vue"
	
	export default {
		components:{typewriter},
		data() {
			return {
				seta: false,
				setb: false,
				setc: false,
				isNext: false,
				innerAudioContext: null,
				innerAudioContextMouse: null,
			}
		},
		beforeDestroy() {
			// this.innerAudioContext && this.innerAudioContext.destroy();
		},
		mounted() {
			// todo 
			
			
		},
		methods: {
			stopAudio() {
				this.innerAudioContext && this.innerAudioContext.destroy();
			},
			stopAudioMouse() {
				this.innerAudioContextMouse && this.innerAudioContextMouse.destroy();
			},
			next() {
				if(this.isNext) {
					this.$emit('pageEvent', {pageIndex: 3})
				}
			},
			finished1() {
				this.seta = true
			},
			finished2() {
				this.setb = true
			},
			finished3() {
				this.setc = true;
			},
			finished4() {
				this.setd = true;
				this.isNext = true;
			},
		}
	}
</script>

<style lang="scss">
	.page122-wrap {
		min-height: 100vh;
		background-image: url(https://datastream.affectai.cn/callmaterial/images/page2/bg-2.png);
		background-position: center;
		background-size: 100% 100%;
		background-repeat: no-repeat;
		position: relative;
		
		.content {
			position: absolute;
			top: 0;right: 0;left: 0;bottom: 0;margin: auto;
			width: 310px;
			height: 230px;
			text-align: center;
		}
	}
</style>